const WebNotificationTest = () => {
  const [registration, setRegistration] = useState(null);

  useEffect(() => {
    if ("serviceWorker" in navigator) {
      navigator.serviceWorker
        .register("/serviceWorker.js")
        .then(function (reg) {
          console.log("Service Worker  :", reg);
          setRegistration(reg); // registration  
        })
        .catch(function (error) {
          console.log("Service Worker  :", error);
        });
    } else {
      console.log("Service Worker  .");
    }
  }, []);

  const makeNotiTest = () => {
    if (Notification.permission === "granted") {
      const options = {
        body: "  , !",
        icon: "https://developer.mozilla.org/static/img/favicon-32x32.png",
        requireInteraction: true,
      };

      if (registration) {
        // registration  
        registration.showNotification("test", options);
      } else {
        console.log("Service Worker   .");
      }
    } else if (Notification.permission === "denied") {
      console.log("  .   .");
      alert("  .   .");
    } else {
      //       
      //   
      Notification.requestPermission().then((permission) => {
        if (permission === "granted") {
          makeNotiTest(); //        
        } else {
          console.log("  .   .");
          alert("  .   .");
        }
      });
    }
  };

  return (
    <div>
      <button onClick={makeNotiTest}> </button>
    </div>
  );
};

export default WebNotificationTest;
